var that;
export default {
	data() {
		return {
			name:'',
			phone:'',
			code:'',
			password1:'',
			password2:'',
			invitarion:''
		}
	},
	
	onLoad() {
		that = this;
	},
	
	methods: {
		
		send_code(){
			if(that.phone.length != 11){
				uni.showToast({
				    title: '请输入正确的手机号',
				    duration: 2000
				});
			}else{
				that.$utils.http(that.$api.send_code,{
					phone:that.phone
				},function(res){
					uni.showToast({
					    title: '发送成功，请注意查收',
					    duration: 2000
					});
				});
			}
		},
		
		register(){
			
			console.log(that.$data)
			
			that.$utils.http(that.$api.register,{
				username:that.name,
				phone:that.phone,
				password:that.password1,
				invitarion:that.invitarion,
				code:that.code
			},function(res){
				uni.showToast({
				    title: '注册成功！',
				    duration: 2000
				});
			})
		}
	}
}
